import tardigrade from "../../Assets/tardigrade.jpg";
import empty from "../../Assets/empty.jpg";
import OldWizzard from "../../Assets/OldWizzard.jpg";
import { Dialog } from "./Dialog";
import { Answer } from "./Answer";

export function detectEncounter(randomEncounter, goto) {
  let encounter = emptyEncounter();
  //let encounter = {};

  if (randomEncounter === "") {
    console.log("patate");
  } else if (randomEncounter.encounterType === "bandit") {
    encounter = banditEncounter(goto);
  } else if (randomEncounter.encounterType === "mage") {
    encounter = mageEncounter(goto);
  }

  return encounter;
}

function emptyEncounter() {
  const scene = new Dialog(empty);
  return scene;
}

function exitScene() {
  const scene = new Dialog(empty);
  const answer = new Answer("exit");
  scene.addAnswer(answer);
  return scene;
}

function banditEncounter(goto) {
  //const exit = "exit";

  const sceneExit = new Dialog(empty);
  const answer = new Answer("exitScene");
  sceneExit.addAnswer(answer);

  const sceneA = new Dialog(tardigrade, "Hello hello voisin");
  //const answerA1 = new Answer("Laisser ce magnifique animal", sceneExit);
  const answerA1 = new Answer("Laisser ce magnifique animal", emptyEncounter());

  sceneA.addAnswer(answerA1);

  if (goto == null) {
    goto = sceneA;
  }

  return goto;
}

function mageEncounter(goto) {
  const sceneA = new Dialog(
    OldWizzard,
    "Bonjour aventurier, je suis un vieux mage qui sert de test pour le dveloppement de rencontres avec les PNJ, j'ai volontairement un texte trs long qui a pour but de tester que ce dernier ne dpasse pas de la <div> qui le contient, si tu arrives  tout lire c'est que a fonctionne comme prvu, d'ailleurs pour les besoin du test j'ai dcid d'augmenter grandement la taille de ce que je raconte, juste pour voir si la div s'accord ou si texte diminue, je ne sais aps encore comment a va s'afficher je vais voir",
  );

  const sceneB = new Dialog(
    OldWizzard,
    "Ceci est un test de mise  jour du text que je raconte, si tu le lis c'est que a s'est correctement mis  jour",
  );

  const sceneC = new Dialog(
    tardigrade,
    "Moi je suis un test pour voir ce qu'il ce passe si on change l'image de la scne, si tu me vois c'est que a fonctionne bien",
  );

  const answerA1 = new Answer(
    "Rponse gnrique qui renvoi vers la scne B",
    sceneB,
  );
  sceneA.addAnswer(answerA1);

  const answerA2 = new Answer(
    "Rponse gnrique qui renvoi vers la scne C",
    sceneC,
  );
  sceneA.addAnswer(answerA2);

  const answerA3 = new Answer(
    "Rponse gnrique, si tu me clique dessus a met fin  ce dialogue",
    emptyEncounter(),
  );
  sceneA.addAnswer(answerA3);

  const answerB1 = new Answer(
    "Rponse gnrique, si tu me clique dessus a met fin  ce dialogue",
    emptyEncounter(),
  );
  sceneB.addAnswer(answerB1);

  const answerC1 = new Answer(
    "Quitter ce dlicieux personnage",
    emptyEncounter(),
  );
  sceneC.addAnswer(answerC1);

  if (goto == null) {
    goto = sceneA;
  }

  return goto;
}

